import { Component, OnInit } from "@angular/core";
import { Message } from "primeng/primeng";
import { CusOperationHttpService } from "app/pages/tzb/custom/http/custom-operation/custom-operation.http.service";
import { ActivatedRoute } from '@angular/router';
import * as _ from 'lodash';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { FormGroup, FormBuilder } from "@angular/forms";
import { Customtools } from "app/pages/tzb/custom/http/customUtil";
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { API } from "app/pages/tzb/custom/http/custom-view/custom-view.constant";
import * as $ from 'jquery';
import { TZB_HTTP_CUS1 } from 'app/@uisftech/common/service/http.constant';
import { CusViewHttpService } from "app/pages/tzb/custom/http/custom-view/custom-view.http.service";

@Component({
    selector: 'group-evaluator-element',
    templateUrl: './group-evaluator-element.component.html',
    styleUrls: ['./group-evaluator-element.component.scss'],
    providers: [CusOperationHttpService, Commfunc]
})
export class GroupEvaluatorElement implements OnInit {
    userform: FormGroup;
    detailList: any = [];//基本数据
    user: any;
    user1: any;
    userId: string;
    userName: string;
    orgId: string;
    orgName: string;
    handleDate: string;
    code: any;
    msgs: Message[];
    nameList: any = [];
    noPromptDispaly: boolean = false;
    adjustmentTypeList: any = [];
    adjustmentModeList: any = [];
    consumeOverdrawQuotaType: any = [];
    adjustOrientation: any = [];
    tablelist: any = [];
    taskRate: string;
    canUp: boolean = false;
    flag: string;
    isHistory: boolean = false;
    integralInfo: any;

    tabMesg: any = tableMessage;//表格中无数据时显示的内容
    beanExam: any;
    roleTypefyList: any;//评价人角色
    updateReasonList: any;//失效
    user2: any;//用户信息
    today = new Date();//日期
    bank: boolean = false;//其他
    bankShow: boolean = false;//其他
    addShow: boolean = false;//新增
    deleteShow: boolean = false;//失效
    waitSelUserList: any;
    //综合授信报告
    TZB_HTTP_CUS1 = TZB_HTTP_CUS1;
    progress: boolean = false;
    reportDisplay: boolean = false;
    reportCustNm: string;//客户号
    inValueReportId: string;
    idenType: string;//证件类型
    idenNum: string;//证件号
    groupName: string;//群名称
    ownerId: string;//群主
    ownerName: string;//群主名称
    constructor(
        public commfunc: Commfunc,
        public routeInfo: ActivatedRoute,
        public httpService: CusOperationHttpService,
        public httpService2: CusViewHttpService,
        private commonFunc: Customtools,
        public fb: FormBuilder,
    ) {
        // 获取参数
        this.detailList = this.routeInfo.snapshot.params;
        this.beanExam = JSON.parse(this.routeInfo.snapshot.params.dataMap);
        // this.bean = JSON.parse(this.routeInfo.snapshot.params.dataMap)
        this.taskRate = this.routeInfo.snapshot.params['taskRate'];
        this.flag = this.routeInfo.snapshot.params['markFlag'];
        if (this.flag == '1') {
            this.isHistory = false;
        } else {
            this.isHistory = true;
        }
        if (this.taskRate == '1' && !this.isHistory) {
            this.canUp = true;
        } else {
            this.canUp = false;
        }
        this.userform = fb.group({
            custId: [''],//客户号
            custName: [''],//客户名称
            roleType: ['4'],//评价人角色
            groupAdminRole: [''],//评价人角色
            statusId: [''],//状态
            // batchId: [''],//批次号
            creditReportNum: ['1'],//信用报告编号
            operationTellerNo: [''],//经办人编号
            operationTellerName: [''],//经办人名称
            operationOrgName: [''],//经办机构
            operationDataTime: [''],//经办时间
            introduction: [''],//评价人简历
            remark: [''],//备注
            updateReason: [''],//原因
            custGroupId: [''],//群号
            groupName: [''],//群名称
            ownerId: [''],//群主
            ownerName: [''],//群主名称
            fromDate: [''],//
            partyClassificationGroupId: [''],//群号
        })
    }
    ngOnInit() {
        this.codeValues();
        if (!this.beanExam.updateReason) {
            this.addShow = true;
            this.bankShow = true;
            this.userform.get('operationTellerName').setValue(this.beanExam.operationTellerName);
            this.userform.get('operationOrgName').setValue(this.beanExam.ownerName);
            this.userform.get('operationTellerNo').setValue(this.beanExam.operationTellerNo);
            this.userform.get('introduction').setValue(this.beanExam.introduction);
            this.userform.get('operationDataTime').setValue(this.beanExam.operationDataTime);
        } else if (this.beanExam.updateReason) {
            this.deleteShow = true;
            this.userform.get('updateReason').setValue(this.beanExam.updateReason);
        }
        this.userform.get('partyClassificationGroupId').setValue(this.beanExam.partyClassificationGroupId);
        this.userform.get('groupAdminRole').setValue(this.beanExam.groupAdminRole);
        this.userform.get('custId').setValue(this.beanExam.custId);
        this.userform.get('custName').setValue(this.beanExam.custName);
        this.userform.get('custGroupId').setValue(this.beanExam.custGroupId);
        this.userform.get('groupName').setValue(this.beanExam.groupName);
        this.userform.get('ownerId').setValue(this.beanExam.ownerId);
        this.userform.get('ownerName').setValue(this.beanExam.ownerName);
        if (this.beanExam.remark) {
            this.userform.get('remark').setValue(this.beanExam.remark);
        }
        this.roleTypefyList = this.code['roleTypefy'];
        this.updateReasonList = this.code['updateReason'];
        this.partydetail();
        this.isroleType();
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    //评价人角色
    isroleType() {
        if (!this.beanExam.updateReason) {
            for (let e = 0; e < this.beanExam.groupAdminRole.length; e++) {
                if (this.beanExam.groupAdminRole[e] == '4') {
                    this.bankShow = false;
                    this.bank = true;
                    return
                } else {
                    this.bankShow = true;
                    this.bank = false;
                }
            }
            if (this.beanExam.groupAdminRole.length == '0') {
                this.bankShow = true;
                this.bank = false;
            }
        }
    }
    //客户号事件
    partydetail() {
        const param = {};
        param['custNo'] = this.beanExam.custId;
        this.httpService.selectCustBriefInfo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.userform.get('custName').setValue(data.custName);
                this.idenType = data.idenType;
                this.idenNum = data.idenNum;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
        });
    }
    //查询
    doLoke() {
        let param = { custNo: this.userform.value.custId }
        let params = this.commfunc.handleParams(param)
        let TZB_HTTP_CUS1 = this.TZB_HTTP_CUS1 + API.creditReportCheck;
        let _self = this;
        $.ajax({
            url: TZB_HTTP_CUS1,
            type: 'POST',
            data: JSON.stringify(params),
            async: false,
            headers: {
                'x-ajax': '1',
                'Content-Type': 'application/json'
            },
            dataType: 'json',
            success: function (data) {
                if (data.returnCode.code == 'success') {
                    if (data.isCreate == "1") {
                        let userName = JSON.parse(_self.commfunc.getSessionDataCH('chName')).userName;
                        _self.creatMainMsg({ custNo: _self.userform.value.custId, modNum: data.updatedNum, custName: _self.userform.value.custName, qryUserId: _self.beanExam.userId, cretType: _self.idenType, cretNo: _self.idenNum, qryUserName: userName, crdtReportNo: data.crdtReportNo, accessWay: '1' })
                    } else {
                        _self.inValueReportId = data.reportId;
                        _self.reportCustNm = _self.userform.value.custId;
                        _self.reportDisplay = true;
                    }
                } else {
                    _self.msgs = [];
                    _self.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            },
            error: function (err) {
            }
        })
    }
    creatMainMsg(param) {
        this.progress = true;
        let params = this.commfunc.handleParams(param);
        this.httpService2.createReportMainInfo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.inValueReportId = data.reportId;
                this.reportCustNm = this.userform.value.custId;
                this.progress = false;
                this.reportDisplay = true;
            } else {
                this.progress = false;
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.progress = false;
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        })
    }
}