import { Component, OnInit, Output, Input, EventEmitter, OnChanges } from '@angular/core';
import { SelectItem, Message } from 'primeng/primeng';
import * as _ from 'lodash';
import { GroupRecProductInfoBean } from './../../bean/custom-group.bean';
import { CusOperationHttpService } from './../../../http/custom-operation/custom-operation.http.service';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { ActivatedRoute, Params, Router } from '@angular/router';
import { addGroupRecProductInfoBean } from './bean/GroupRecProductInfo.bean';
import { tableMessage } from 'app/@uisftech/common/constant/config';


@Component({
    selector: 'group-recProduct-info',
    templateUrl: './group-recProduct-info.html',
    styleUrls: ['./group-recProduct-info.scss'],
    providers: [Commfunc]
})
export class GroupRecProductOInfoComponent implements OnInit, OnChanges {
    @Output() outValue: EventEmitter<any> = new EventEmitter<any>();
    @Input() inValue: any;
    @Input() page: any;
    @Input() page2: any;
    @Input() groupValue: any;
    groupValueFlag: boolean = true;//群类型判断
    //公共
    tableMesg: any = tableMessage;
    // 标题
    title: any = '主推产品'
    //group_id
    groupId: any;
    // 控件
    msgs: Message[] = [];
    first: any = 0;
    // 模态框
    display: boolean = false;

    // bean
    groupRecProductInfoBean: GroupRecProductInfoBean = new GroupRecProductInfoBean();
    addGroupRecProductInfoBean: addGroupRecProductInfoBean = new addGroupRecProductInfoBean();

    // 中间量
    list: any = [];
    productName: any = '';
    // 取session
    createrId: any = '';
    createrName: any = '';
    createDate: any = '';
    //获取所有码值
    code: any;
    productList: any;// = productList;
    newproduct_List: any = [];//主推产品
    constructor(
        public httpService: CusOperationHttpService,
        public router: Router,
        public commfunc: Commfunc,
    ) {
        this.codeValues() //调用方法，获取全部码值
        //获取所需要的码值,中括号中的变量是自己在原来码值表中的变量名！！！！
        this.productList = this.code['productList'];
    }
    ngOnInit() {
        this.groupId = this.page2.groupId;
        this.preSet();

    }
    ngOnChanges() {
        if (this.groupValue && this.groupValue == '110') {
            this.groupValueFlag = false;
        }
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    preSet() {
        // 取session
        let session = window.sessionStorage;
        let data = session.getItem('commonHeader');
        if (!data) {
            this.router.navigate(["/login"]);
            return;
        }
        let mesg = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (!mesg) {
        }
        let ch = JSON.parse(this.commfunc.getSessionDataCH('chName'));
        this.createrId = mesg['userId'];//创建人id
        this.createrName = ch['userName'];//创建人名
        // 取当前日期
        let date = new Date();
        this.createDate = this.commfunc.transDate(date);
    }

    selectGroupRecProductInfo() {
        // 客户群主推产品查询
        console.log(this.page)
        this.httpService.selectGroupRecProductInfo(this.page).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.inValue = data.groupProductList;
                // this.inValue['groupId'] = data.groupId;
            }
            else {
                this.msgs = [];
                data.returnCode.message ? data.returnCode.message : '查询主推产品失败！';
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败！' }];
        })
    }
    paginate(event) {
        // 分页
        this.page.pageSize = event.rows;
        let currentPage = event.page + 1;
        this.page.pageNum = currentPage;
        this.first = (currentPage - 1) * this.page.pageSize;
        this.selectGroupRecProductInfo();//客户群主推产品查询
    }
    add() {
        this.display = true;
    }
    addCall(event) {
        this.newproduct_List = [];
        let items_param = JSON.parse(JSON.stringify(event));
        let arrar = {
            productCode: '',
            productName: '',
        };
        for (let param of this.inValue) {
            arrar['productCode'] = param.productCode;
            arrar['productName'] = param.productName;
            let temp = JSON.parse(JSON.stringify(arrar));
            this.newproduct_List.push(temp)
        }
        let flag = false;
        for (let i = 0; i < items_param.length; i++) {
            for (let k = 0; k < this.newproduct_List.length; k++) {
                if (items_param[i].productCode == this.newproduct_List[k].productCode) {
                    flag = true;
                    break
                }
            }
            if (!flag) {
                this.newproduct_List.push(items_param[i])
            }
        }
        console.log(this.newproduct_List)
        // 添加
        //  产品码赋值
        console.log(event)
        this.display = false;
        this.addGroupRecProductInfoBean.productCodeAndName = this.newproduct_List;
        // this.addGroupRecProductInfoBean.productCode = _.clone(this.productName);
        this.addGroupRecProductInfoBean.groupId = _.clone(this.groupId);
        this.addGroupRecProductInfoBean.createDate = _.clone(this.createDate);
        this.addGroupRecProductInfoBean.createrId = _.clone(this.createrId);
        this.addGroupRecProductInfoBean.createrName = _.clone(this.createrName);

        // 重复判断
        // let flag1 = true;
        // flag1 = this.isHave();
        // if (!flag1) {
        //     return;
        // }
        this.addGroupRecProductInfo();
    }
    // isHave() {
    //     // 是否重复
    //     let flag = true;
    //     if (this.inValue) {
    //         this.inValue.forEach(item => {
    //             if (item.productCode == this.addGroupRecProductInfoBean.productCode) {
    //                 this.addGroupRecProductInfoBean.productCode = '';
    //                 this.addGroupRecProductInfoBean.productName = '';
    //                 this.msgs = [];
    //                 this.msgs = [{ severity: 'info', summary: '提示', detail: '不能重复添加！' }];
    //                 flag = false;
    //                 return;
    //             }
    //         })
    //     }
    //     return flag;
    // }
    addGroupRecProductInfo() {
        // 新增主推产品
        this.addGroupRecProductInfoBean.groupId = this.groupId;
        this.httpService.addGroupRecProductInfo(this.addGroupRecProductInfoBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.msgs = [];
                this.msgs = [{ severity: 'success', summary: '提示', detail: '新增成功' }];
                this.display = false;
                this.page.pageSize = 10;
                this.page.pageNum = 1;
                this.first = 0;
                this.selectGroupRecProductInfo();//客户群主推产品查询
            }
            else {
                this.msgs = [];
                data.returnCode.message ? data.returnCode.message : '查询主推产品失败！';
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败！' }];
        })
    }
    deleteGroupRecProductInfo(item) {
        let params = {
            groupId: this.groupId,
            productCode: item.productCode
        }
        // 新增主推产品
        this.httpService.deleteGroupRecProductInfo(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.msgs = [];
                this.msgs = [{ severity: 'success', summary: '提示', detail: '删除成功' }];
                this.display = false;
                this.page.pageSize = 10;
                this.page.pageNum = 1;
                this.first = 0;
                this.selectGroupRecProductInfo();//客户群主推产品查询
            }
            else {
                this.msgs = [];
                // data.returnCode.message ? data.returnCode.message : '查询主推产品失败！';
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败！' }];
        })
    }
    updateGroupRecProductInfo() {
        // let params = {
        //     groupId : this.groupId,
        //     productCode: item.productCode
        //  }
        // 修改主推产品
        //  产品码赋值
        this.productList.some(item => {
            if (item.value == this.inValue[0].productCode) {
                this.inValue[0].productName = _.clone(item.label);
                this.inValue[0]['groupId'] = this.groupId;
                return false;
            }
        })
        this.httpService.updateGroupRecProductInfo(this.inValue[0]).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.msgs = [];
                this.msgs = [{ severity: 'success', summary: '提示', detail: '修改成功' }];
                this.display = false;
                this.page.pageSize = 10;
                this.page.pageNum = 1;
                this.first = 0;
                this.selectGroupRecProductInfo();//客户群主推产品查询
            }
            else {
                this.msgs = [];
                // data.returnCode.message ? data.returnCode.message : '查询主推产品失败！';
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败！' }];
        })
    }
    productChange(col) {
    }

    return() {
        // 返回
        this.list = [];
        if (this.inValue) {
            this.inValue.forEach(item => {
                this.list.push(item.productName);
            })
        }
        this.outValue.emit({ GroupRecProductInfoDisplay: false, groupRecProductChecked: this.list })
    }
    //按钮权限
    permissionCheck(id) {
        return this.commfunc.permissionCheck(id)
    }
}