  //----------------------20171201
  selectCustGroupInfo() {
    this.isHasDel = false;
    let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    let parames = {
      groupId: this.groupId
    }
    // 客户群管理人员查询
    // this.httpService.selectGroupAdminInfo(this.groupAdminInfoBean).subscribe(data => {
    this.httpService.selectCustGroupInfo(parames).subscribe(data => {//1201
      if (data.returnCode.code == 'success') {
        // console.log(data)
        this.orgId = data.orgId
        let stu_1 = false;
        let stu_2 = false;
        if (data.ownerId == commonHeader.userId) {
          stu_1 = true;
        }
        stu_2 = data.contactList.some((item) => {
          return item.roleId == commonHeader.userId;
        })
        if (stu_1 || stu_2) {
          this.isHasDel = true;
        }
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, err => {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
    })
  }

