import { Component, OnInit, Input } from '@angular/core';
import { Message } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { CusOperationHttpService } from './../../../http/custom-operation/custom-operation.http.service';
import { ActivatedRoute } from '@angular/router';
import { CustGroupInfoBean } from "app/pages/tzb/custom/custom-group/bean/custom-group.bean";
@Component({
  selector: 'performance',
  templateUrl: './performance.component.html',
  styleUrls: ['./performance.component.scss'],
  providers: [Commfunc, CusOperationHttpService]
})
export class PerformanceComponent implements OnInit {
  @Input() groupId: any;
  @Input() title_1: any;//主推产品名称
  @Input() lablel3: any;//主推产品名称
  title_2: string = '业绩趋势';
  title2: string = '户数趋势';
  // title_1: string = '存贷款业绩|存贷款业绩';
  // title2: string = '客户户数|有效户数|营销人数';
  custGroupInfoBean: CustGroupInfoBean = new CustGroupInfoBean();//客户群信息  
  data1: any;
  msgs1: Message[];
  data2: any;
  msgs2: Message[];
  zero : any = 0;
  public msgs: any = [];
  public lablel_left: string = '存款月日均（单位：万元）';
  public lablel_left2: string = '贷款月日均（单位：万元）';
  // public lablel_left3: string = '主推产品月均余额（单位：万元）';
  public lablel_right: string = '户数';
  public lablel_right2: string = '客户数';
  public lablels: any = [];
  public datasets_left: any = [];
  public datasets_left2: any = [];
  public datasets_left3: any = [];
  public datasets_right: any = [];
  public datasets_right2: any = [];
  public datasets_right3: any = [];
  public datasets_right4: any = [];
  public datasets_right5: any = [];
  public datasets_right6: any = [];
  public x: any = [];
  constructor(
    public commfunc: Commfunc,
    public httpservice: CusOperationHttpService,
    public ActivatedRoute: ActivatedRoute
  ) { }

  ngOnInit() {
    // this.params = this.ActivatedRoute.snapshot.params
    // this.selectGroupAchiDemogInfo();
    // this.selectCustGroupInfo();
    this.refresh();
  }
  refresh(){
    this.selectGroupAchiDemogInfo();
    this.selectCustGroupInfo();
  }
  selectData1(event) {
    this.msgs1 = [];
    this.msgs1.push({ severity: 'info', summary: 'Data Selected', 'detail': this.data1.datasets[event.element._datasetIndex].data[event.element._index] });
  }
  selectData2(event) {
    this.msgs1 = [];
    this.msgs1.push({ severity: 'info', summary: 'Data Selected', 'detail': this.data2.datasets[event.element._datasetIndex].data[event.element._index] });
  }
  //客户群统计业务查询
  selectGroupAchiDemogInfo() {
    let params = {
      "groupId": this.groupId
    }
    let successFnc = data => {
      data.infoList.forEach(item => {
        this.datasets_left.push(item.mdeptAvgBal);
      });
      data.infoList.forEach(item => {
        this.datasets_left2.push(item.mloanAvgBal);
      });
      // data.infoList.forEach(item => {
      //   this.datasets_left3.push(item.mprodAvgBal);
      // });

      //户数； doorNum
      //有效客户数；
      //存款开户数（存款账户有效，正常或冻结状态；多个只算一个）； deptOpenNum
      //目标客户数（名单导入时的是否目标客户字段为是）； targetCustNum
      //授信客户数（贷款合同，一次性已执行（表内贷款余额大于0），循环未执行，已执行，冻结等为贷款有效合同）；  crdtCustNum
      //预授信客户数（预授信登记薄，有效的，否决的不包含） creditFlag
      data.infoList.forEach(item => {
        this.datasets_right.push(item.doorNum);
      });
      data.infoList.forEach(item => {
        this.datasets_right2.push(item.deptOpenNum);
      });
      data.infoList.forEach(item => {
        this.datasets_right3.push(item.targetCustNum);
      });
      data.infoList.forEach(item => {
        this.datasets_right4.push(item.crdtCustNum);
      });
      data.infoList.forEach(item => {
        this.datasets_right5.push(item.creditFlag);
      });
      data.infoList.forEach(item => {
        this.datasets_right6.push(item.validCustNum);
      });
      data.infoList.forEach(item => {
        this.x.push(item.dt);
      });
      //存款月日均；贷款月日均；主推产品月日均
      this.data1 = {
        labels: this.x,
        datasets: [
          {
            label: this.lablel_left,
            data: this.datasets_left,
            fill: false,
            borderColor: '#00c1e0'
          },
          {
            label: this.lablel_left2,
            data: this.datasets_left2,
            fill: false,
            borderColor: '#fdd100'
          },
          // {
          //   label: this.lablel3 + "日均余额（单位：万元）",
          //   data: this.datasets_left3,
          //   fill: false,
          //   borderColor: '#2196f3'
          // }
        ]
      }
      //户数； doorNum
      //有效客户数；
      //存款开户数（存款账户有效，正常或冻结状态；多个只算一个）； deptOpenNum
      //目标客户数（名单导入时的是否目标客户字段为是）； targetCustNum
      //授信客户数（贷款合同，一次性已执行（表内贷款余额大于0），循环未执行，已执行，冻结等为贷款有效合同）；  crdtCustNum
      //预授信客户数（预授信登记薄，有效的，否决的不包含） creditFlag
      this.data2 = {
        labels: this.x,
        datasets: [
          {
            label: '户数',
            data: this.datasets_right,
            fill: false,
            borderColor: '#f49e51'
          },
          {
            label: '存款开户数',
            data: this.datasets_right2,
            fill: false,
            borderColor: '#3994db'
          },
          {
            label: '目标客户数',
            data: this.datasets_right3,
            fill: false,
            borderColor: '#38c280'
          },
          {
            label: '授信客户数',
            data: this.datasets_right4,
            fill: false,
            borderColor: '#0c6287'
          },
          {
            label: '预授信客户数',
            data: this.datasets_right5,
            fill: false,
            borderColor: '#00c1e0'
          },
          {
            label: '有效客户数',
            data: this.datasets_right6,
            fill: false,
            borderColor: '#e62cd6'
          },
        ]
      }
    }

    this.commfunc.interfaceComFnc(this.httpservice.selectGroupAchiDemogInfo(params), successFnc, this.msgs)
  }


    // 查询
    selectCustGroupInfo() {
      let params = {
        groupId : this.groupId  
      }
      // 客户群信息查询
      this.httpservice.selectCustGroupInfo(params).subscribe(data => {
        if (data.returnCode.code == 'success') {
          this.custGroupInfoBean = data;//客户群信息查询bean
        } else {
          this.msgs = [];
          data.returnCode.code ? data.returnCode.code : '调用服务失败！';
          this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
        }
      }, err => {
        this.msgs = [];
        this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败！' }];
      })
    }
  //按钮权限
	permissionCheck(id){
		return	this.commfunc.permissionCheck(id)
	  }

}
