import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { CustomGroupViewComponent } from "./custom-group-view.component";
import { CustomImageQueryComponent } from './custom-image-query/custom-image-query.component';
import { ApprovedItemsComponent } from '../../reminder-do/backlog-detail/btn-page/approve-page/approved-items/approved-items.component';
import { WorktaskComponent } from 'app/pages/tzb/custom/reminder-do/backlog-detail/btn-page/workdesk-page/worktask/worktask.component';
import { RemindersComponent } from 'app/pages/tzb/custom/reminder-do/backlog-detail/btn-page/tip-page/reminders/reminders.component';
import { GroupMarketingPlanComponent } from './group-marketing-plan/group-marketing-plan.component';
import { customGroupListExamComponent } from 'app/pages/tzb/custom/custom-group/custom-group-view/customGroup-list-exam/customGroup-list-exam';
import { GroupEvaluator } from "app/pages/tzb/custom/custom-group/custom-group-view/group-evaluator/group-evaluator.component";
import { GroupEvaluatorDetail } from "app/pages/tzb/custom/custom-group/custom-group-view/group-evaluator-detail/group-evaluator-detail.component";
import { GroupEvaluatorSummary } from "app/pages/tzb/custom/custom-group/custom-group-view/group-evaluator-summary/group-evaluator-summary.component";
import { GroupEvaluatorStatistical } from "app/pages/tzb/custom/custom-group/custom-group-view/group-evaluator-statistical/group-evaluator-statistical.component";
import { GroupOrgStatistical } from "app/pages/tzb/custom/custom-group/custom-group-view/group-org-statistical/group-org-statistical.component";
import { GroupCustomStatistical } from "app/pages/tzb/custom/custom-group/custom-group-view/group-custom-statistical/group-custom-statistical.component";
import { GroupExtensionStatistical } from "app/pages/tzb/custom/custom-group/custom-group-view/group-extension-statistical/group-extension-statistical.component";
import { GroupEvaluatorElement } from "app/pages/tzb/custom/custom-group/custom-group-view/group-evaluator-element/group-evaluator-element.component";
import { CustomGroupMapComponent } from "app/pages/tzb/custom/custom-group/custom-group-view/custom-group-map/custom-group-map.component";

//客户群组
// import { CustomGroupDeleteComponent } from './custom-group-delete/custom-group-delete.component';//客户群-删除客户群
export const routes: Routes = [
  {
    path: '',
    component: CustomGroupViewComponent,//创建
    children: [
      { path: "approved-items", component: ApprovedItemsComponent },
      { path: "worktask", component: WorktaskComponent },
      { path: "reminders", component: RemindersComponent },
    ]
  },
  { path: 'custom-image-query', component: CustomImageQueryComponent },
  { path: 'group-marketing-plan', component: GroupMarketingPlanComponent },//客户群营销方案
  { path: 'customGroup-list-exam', component: customGroupListExamComponent },//客户群名单审批
  { path: 'group-evaluator', component: GroupEvaluator },//客户群第三方查询
  { path: 'group-evaluator-detail', component: GroupEvaluatorDetail },//客户群第三方明细
  { path: 'group-evaluator-summary', component: GroupEvaluatorSummary },//客户群第三方汇总
  { path: 'group-evaluator-statistical', component: GroupEvaluatorStatistical },//客户群第三方统计
  { path: 'group-org-statistical', component: GroupOrgStatistical },//客户明细归属统计
  { path: 'group-custom-statistical', component: GroupCustomStatistical },//客群名单明细统计
  { path: 'group-extension-statistical', component: GroupExtensionStatistical },//客群目标授信明细统计
  { path: 'group-evaluator-element', component: GroupEvaluatorElement },//客群目标授信明细统计
  { path: 'customGroup-list-map', component: CustomGroupMapComponent }, // 群定位地图
]

export const CustomGroupViewRouting: ModuleWithProviders = RouterModule.forChild(routes);
