
import { Component, OnInit, Output, EventEmitter, Input } from "@angular/core";
import { Message } from "primeng/primeng";
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { CusOperationHttpService } from "app/pages/tzb/custom/http/custom-operation/custom-operation.http.service";
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { specialSymbol, checkCard } from "app/@uisftech/common/service/validator";
import { Customtools } from "app/pages/tzb/custom/http/customUtil";
import * as $ from 'jquery';
import { API } from "app/pages/tzb/custom/http/custom-view/custom-view.constant";
import { CusViewHttpService } from "app/pages/tzb/custom/http/custom-view/custom-view.http.service";
import { TZB_HTTP_CUS1 } from 'app/@uisftech/common/service/http.constant';
import { ActivatedRoute } from "@angular/router";
import { tableMessage } from "app/@uisftech/common/constant/config";

@Component({
    selector: 'group-evaluator-add',
    templateUrl: './group-evaluator-add.component.html',
    styleUrls: ['./group-evaluator-add.component.scss'],
})
export class GroupEvaluatorAdd implements OnInit {
    @Output() outValue = new EventEmitter();
    @Input() inValue: any;
    @Input() modifyValue: any;
    @Input() groupId: any;
    userform: FormGroup;
    msgs: Message[] = [];
    code: any;
    roleTypefyList: any;//评价人角色
    updateReasonList: any;//失效
    user: any;//用户信息
    user2: any;//用户信息
    bank: boolean = false;//其他
    bankShow: boolean = false;//其他
    dealPerShow: boolean = false;
    addShow: boolean = false;//新增
    deleteShow: boolean = false;//失效
    waitSelUserList: any;
    outVal: any;
    //综合授信报告
    TZB_HTTP_CUS1 = TZB_HTTP_CUS1;
    progress: boolean = false;
    reportDisplay: boolean = false;
    reportCustNm: string;//客户号
    inValueReportId: string;
    idenType: string;//证件类型
    idenNum: string;//证件号
    groupName: string;//群名称
    ownerId: string;//群主
    ownerName: string;//群主名称
    tabMesg: any = tableMessage;//表格中无数据时显示的内容
    idenTypeList: any;//证件类型
    display: boolean = false;
    first: number = 0;//分页控制
    pageNum: number = 1;//当前页数
    pageSize: number = 10;//每页条数
    totalNum: number;//总条数
    dataList: any = [];
    date = new Date();
    constructor(
        public httpService: CusOperationHttpService,
        public httpService2: CusViewHttpService,
        public routerInfo: ActivatedRoute,
        private commonFunc: Customtools,
        public commfunc: Commfunc,
        public fb: FormBuilder,
    ) {
        this.groupId = this.routerInfo.snapshot.params['groupId'];
        this.groupName = this.routerInfo.snapshot.params['groupName'];
        this.ownerId = this.routerInfo.snapshot.params['ownerId'];
        this.ownerName = this.routerInfo.snapshot.params['ownerName'];
        this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        this.user2 = JSON.parse(this.commfunc.getSessionDataCH('chName'));
        this.userform = fb.group({
            custId: [''],//客户号
            custName: ['', Validators.required],//客户名称
            roleType: ['4'],//评价人角色
            groupAdminRole: ['', Validators.required],//评价人角色
            statusId: [''],//状态
            // batchId: [''],//批次号
            creditReportNum: [''],//信用报告编号
            operationTellerNo: [''],//经办人编号
            operationTellerName: [''],//经办人名称
            operationOrg: [''],//经办机构
            operationOrgName: [''],//经办机构
            operationDataTime: [''],//经办时间
            introduction: ['', [Validators.required, Validators.maxLength(255), specialSymbol]],//评价人简历
            remark: [''],//备注
            updateReason: ['', Validators.required],//原因
            custGroupId: [''],//群号
            groupName: [''],//群名称
            ownerId: [''],//群主
            ownerName: [''],//群主名称
            fromDate: [''],//
        })
    }
    ngOnInit() {
        this.codeValues() //调用方法，获取全部码值
        this.roleTypefyList = this.code['roleTypefy'];
        this.updateReasonList = this.code['updateReason'];
        this.idenTypeList = this.code['CustomGroupIdenType'];
        if (this.modifyValue == '1') {
            this.addShow = true;
            this.bankShow = true;
            this.deleteShow = false;
            this.userform.removeControl('updateReason');
            this.userform.get('operationTellerName').setValue(this.user2.userName);
            this.userform.get('operationOrgName').setValue(this.user2.orgName);
            this.userform.get('operationTellerNo').setValue(this.user.userId);
            this.userform.get('operationOrg').setValue(this.user.extParam.orgId);
            this.userform.get('operationDataTime').setValue(this.commfunc.getNowFormatDate());
        } else if (this.modifyValue == '2') {
            this.deleteShow = true;
            this.addShow = false;
            this.userform.get('custId').setValue(this.inValue.userId);
            this.userform.get('custName').setValue(this.inValue.userName);
            this.userform.get('groupAdminRole').setValue(this.inValue.groupAdminRole);
            this.userform.get('creditReportNum').setValue(this.inValue.creditReportNum);
            this.userform.addControl('updateReason', this.fb.control('', [Validators.required]));
            this.userform.removeControl('introduction');
        }
        this.userform.get('custGroupId').setValue(this.groupId);
        this.userform.get('groupName').setValue(this.groupName);
        this.userform.get('ownerId').setValue(this.ownerId);
        this.userform.get('ownerName').setValue(this.ownerName);
        this.userform.get('fromDate').setValue(this.commfunc.getNowFormatDate());
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    //评价人角色
    isroleType() {
        if (this.modifyValue == '1') {
            for (let e = 0; e < this.userform.value.groupAdminRole.length; e++) {
                if (this.userform.value.groupAdminRole[e] == '4') {
                    this.bankShow = false;
                    this.bank = true;
                    this.userform.addControl('remark', this.fb.control('', [Validators.required, Validators.maxLength(255), specialSymbol]));
                    return
                } else {
                    this.bankShow = true;
                    this.bank = false;
                }
            }
            if (this.userform.value.groupAdminRole.length == '0') {
                this.bankShow = true;
                this.bank = false;
            }
        }
    }
    //查询
    doLoke() {
        let param = { custNo: this.userform.value.custId }
        let params = this.commfunc.handleParams(param)
        let TZB_HTTP_CUS1 = this.TZB_HTTP_CUS1 + API.creditReportCheck;
        let _self = this;
        $.ajax({
            url: TZB_HTTP_CUS1,
            type: 'POST',
            data: JSON.stringify(params),
            async: false,
            headers: {
                'x-ajax': '1',
                'Content-Type': 'application/json'
            },
            dataType: 'json',
            success: function (data) {
                if (data.returnCode.code == 'success') {
                    if (data.isCreate == "1") {
                        let userName = JSON.parse(_self.commfunc.getSessionDataCH('chName')).userName;
                        _self.creatMainMsg({ custNo: _self.userform.value.custId, modNum: data.updatedNum, custName: _self.userform.value.custName, qryUserId: _self.user.userId, cretType: _self.idenType, cretNo: _self.idenNum, qryUserName: userName, crdtReportNo: data.crdtReportNo, accessWay: '1' })
                    } else {
                        _self.inValueReportId = data.reportId;
                        _self.reportCustNm = _self.userform.value.custId;
                        _self.reportDisplay = true;
                    }
                } else {
                    _self.msgs = [];
                    _self.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            },
            error: function (err) {
            }
        })
    }
    creatMainMsg(param) {
        this.progress = true;
        let params = this.commfunc.handleParams(param);
        this.httpService2.createReportMainInfo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.inValueReportId = data.reportId;
                this.reportCustNm = this.userform.value.custId;
                this.progress = false;
                this.reportDisplay = true;
            } else {
                this.progress = false;
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.progress = false;
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        })
    }
    // 客户查询弹框
    perTreeShow() {
        this.display = true;
        this.userform.get('custId').setValue('');
        this.userform.get('custName').setValue('');
        this.idenType = '';
        this.idenNum = '';
        this.selectCustGroupList();
    }
    // 返显
    onSort(e) {
        this.display = false;
        this.userform.get('custId').setValue(e.custNo);
        this.userform.get('custName').setValue(e.custName);
        this.idenType = e.idenType;
        this.idenNum = e.idenNum;
    }
    // 客户名单查询
    selectCustGroupList() {
        let params = {
            groupId: this.groupId,
            listType: '1',
            statusId: "1",
            pageNum: this.pageNum,
            pageSize: this.pageSize,
        }
        this.httpService.selectCustGroupList(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.dataList = data.custList;
                this.totalNum = data.totalNum;
            }
            else {
                this.msgs = [];
                data.returnCode.code ? data.returnCode.code : '调用服务失败！';
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }]
            }
        }, err => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' })
        })
    }
    paginate(event) {
        //每页显示的条数
        this.pageSize = event.rows;
        this.pageNum = event.page + 1;
        this.first = event.first;
        this.selectCustGroupList();
    }
    //客户号事件
    partydetail() {
        const param = {};
        param['custNo'] = this.userform.value.custId;
        this.httpService.selectCustBriefInfo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.userform.get('custName').setValue(data.custName);
                this.idenType = data.idenType;
                this.idenNum = data.idenNum;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
        });
    }
    // 保存/提交
    doSave(i) {
        if (!this.userform.value.custId) {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '请输入客户号!' }];
            return
        }
        if (!this.userform.value.custName) {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '请输入客户名称!' }];
            return
        }
        if (this.bank == true) {
            if (!this.userform.value.remark) {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '请输入备注!' }];
                return
            }
        }
        if (this.addShow == true) {
            if (!this.userform.value.introduction) {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '请输入评价人简介!' }];
                return
            }
        }
        if (this.deleteShow == true) {
            if (!this.userform.value.updateReason) {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '请输入失效原因!' }];
                return
            }
        }
        if (this.modifyValue == '1') {//新增
            if (!this.userform.value.groupAdminRole) {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '请输入评价人角色!' }];
                return
            }
            if (i == '1') {
                const param = Object.assign({}, this.userform.value);
                param['operationSign'] = '1';
                param['operationType'] = '0';
                param['creditReportNum'] = this.inValueReportId;
                param['groupAdminRole'] = this.userform.value.groupAdminRole.toString();
                this.httpService.groupAdminApply(param).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        this.outValue.emit(false);
                        this.msgs = [];
                        this.msgs = [{ severity: 'success', summary: '提示', detail: '保存成功！' }];
                    } else {
                        this.msgs = [];
                        data.returnCode.message ? data.returnCode.message : '保存失败！';
                        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
                    }
                }, err => {
                    this.msgs = [];
                    this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
                });
            } else if (i == '2') {//修改
                this.waitSelUserList = this.commfunc.query('KHQ_DSFPJRXZ', { processType: 0 });
                if (!this.waitSelUserList.erro) {
                    this.outVal = this.waitSelUserList.waitSelUserList;
                    let perPageShow = this.waitSelUserList.display;
                    if (perPageShow == true) {
                        this.dealPerShow = true;
                    } else {
                        const param = Object.assign({}, this.userform.value);
                        param['operationSign'] = '1';
                        param['operationType'] = '1';
                        param['creditReportNum'] = this.inValueReportId;
                        param['groupAdminRole'] = this.userform.value.groupAdminRole.toString();
                        this.httpService.groupAdminApply(param).subscribe(data => {
                            if (data.returnCode.code == 'success') {
                                this.outValue.emit(false);
                                this.msgs = [];
                                this.msgs = [{ severity: 'success', summary: '提示', detail: '提交成功！' }];
                            } else {
                                this.msgs = [];
                                data.returnCode.message ? data.returnCode.message : '提交失败！';
                                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
                            }
                        }, err => {
                            this.msgs = [];
                            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
                        });
                    }
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: this.waitSelUserList.erro });
                }
            }
        } else if (this.modifyValue == '2') {
            if (i == '1') {
                const param = Object.assign({}, this.userform.value);
                param['operationSign'] = '0';
                param['operationType'] = '0';
                param['groupAdminRole'] = this.userform.value.groupAdminRole.toString();
                this.httpService.groupAdminApply(param).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        this.outValue.emit(false);
                        this.msgs = [];
                        this.msgs = [{ severity: 'success', summary: '提示', detail: '保存成功！' }];
                    } else {
                        this.msgs = [];
                        data.returnCode.message ? data.returnCode.message : '保存失败！';
                        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
                    }
                }, err => {
                    this.msgs = [];
                    this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
                });
            } else if (i == '2') {
                this.waitSelUserList = this.commfunc.query('KHQ_DSFPJRXZ', { processType: this.inValue.processType });
                if (!this.waitSelUserList.erro) {
                    this.outVal = this.waitSelUserList.waitSelUserList;
                    let perPageShow = this.waitSelUserList.display;
                    if (perPageShow == true) {
                        this.dealPerShow = true;
                    } else {
                        const param = Object.assign({}, this.userform.value);
                        param['operationSign'] = '0';
                        param['operationType'] = '1';
                        param['groupAdminRole'] = this.userform.value.groupAdminRole.toString();
                        param['batchId'] = this.inValue.batchId;
                        this.httpService.groupAdminApply(param).subscribe(data => {
                            if (data.returnCode.code == 'success') {
                                this.outValue.emit(false);
                                this.msgs = [];
                                this.msgs = [{ severity: 'success', summary: '提示', detail: '提交成功！' }];
                            } else {
                                this.msgs = [];
                                data.returnCode.message ? data.returnCode.message : '提交失败！';
                                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
                            }
                        }, err => {
                            this.msgs = [];
                            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
                        });
                    }
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: this.waitSelUserList.erro });
                }
            }
        }
    }
    outDisplayCall(event) {
        this.dealPerShow = event;
    }
    outValueCall(event) {
        debugger
        this.outVal = event;
        let ss = '';
        let specifyNextNode = '';
        this.outVal.forEach(item => {
            ss += item.tellerId;
            specifyNextNode = item.userBank;
        });
        if (this.modifyValue == '1') {
            const param = Object.assign({}, this.userform.value);
            param['operationSign'] = '1';
            param['operationType'] = '1';
            param['authUser'] = ss;
            param['author'] = specifyNextNode;
            param['creditReportNum'] = this.inValueReportId;
            param['groupAdminRole'] = this.userform.value.groupAdminRole.toString();
            this.httpService.groupAdminApply(param).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.outValue.emit(false);
                    this.msgs = [];
                    this.msgs = [{ severity: 'success', summary: '提示', detail: '提交成功！' }];
                } else {
                    this.msgs = [];
                    data.returnCode.message ? data.returnCode.message : '提交失败！';
                    this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
                }
            }, err => {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
            });
        } else if (this.modifyValue == '2') {
            const param = Object.assign({}, this.userform.value);
            param['operationSign'] = '0';
            param['operationType'] = '1';
            param['authUser'] = ss;
            param['author'] = specifyNextNode;
            param['batchId'] = this.inValue.batchId;
            if (this.userform.value.groupAdminRole) {
                param['groupAdminRole'] = this.userform.value.groupAdminRole.toString();
            }
            this.httpService.groupAdminApply(param).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.outValue.emit(false);
                    this.msgs = [];
                    this.msgs = [{ severity: 'success', summary: '提示', detail: '提交成功！' }];
                } else {
                    this.msgs = [];
                    data.returnCode.message ? data.returnCode.message : '提交失败！';
                    this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
                }
            }, err => {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
            });
        }
    }
    // 返回
    doReset() {
        this.outValue.emit(false);
    }
}