import { Component, OnInit } from '@angular/core';
import { Message } from 'primeng/primeng';
import { ActivatedRoute } from '@angular/router';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { CusOperationHttpService } from './../../../http/custom-operation/custom-operation.http.service';
import { tableMessage } from 'app/@uisftech/common/constant/config';


@Component({
  selector: 'cross-sales-list',
  templateUrl: './cross-sales-list.component.html',
  styleUrls: ['./cross-sales-list.component.scss'],
  providers: [Commfunc, CusOperationHttpService]
})
export class CrossSalesListComponent implements OnInit {
  //公共
  tableMesg: any = tableMessage;
  items: any[];
  params: any;
  groupId: string;
  pageNum: number = 1;
  pageSize: number = 10;
  totalNum: any;
  msgs: Message[] = [];
  data_info: any;
  constructor(
    public activated_route: ActivatedRoute,
    public Commfnc: Commfunc,
    public httpService: CusOperationHttpService
  ) { }

  ngOnInit() {
    this.params = this.activated_route.snapshot.params;
    if (this.params.hasOwnProperty('id')) {
      this.groupId = this.params.id;
    }
    if (this.params.hasOwnProperty('groupId')) {
      this.groupId = this.params.groupId;
    }
    //获取交叉销售名单
    this.selectCustRecomProdInfo();
  }
  //获取交叉销售名单
  selectCustRecomProdInfo() {
    let params = {
      groupId: this.groupId,
      pageNum: this.pageNum,
      pageSize: this.pageSize
    }
    let successFnc = data => {
      this.totalNum = data.totalNum;
      this.data_info = data.infoList;
    }
    this.Commfnc.interfaceComFnc(this.httpService.selectCustRecomProdInfo(params), successFnc, this.msgs)
  }
  paginate(event) {
    // 分页
    this.pageNum = event.page + 1;
    this.pageSize = event.rows;
    this.selectCustRecomProdInfo();//客户群名单查询
  }
  //按钮权限
  permissionCheck(id) {
    return this.Commfnc.permissionCheck(id)
}
}
