import { Component, OnInit, Input } from '@angular/core';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { Message } from "primeng/components/common/message";
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { tableMessage, CALENDAR_CN } from "app/@uisftech/common/constant/config";
import { CusOperationHttpService } from "app/pages/tzb/custom/http/custom-operation/custom-operation.http.service";
import { Customtools } from "app/pages/tzb/custom/http/customUtil";
import { specialSymbol, checkNumber } from "app/@uisftech/common/service/validator";
import { TZB_CUS_DOWNLOAD } from "app/@uisftech/common/service/http.constant";
import { Router } from "@angular/router";

@Component({
    selector: 'group-org-statistical',
    templateUrl: './group-org-statistical.component.html',
    styleUrls: ['./group-org-statistical.component.scss'],
})
export class GroupOrgStatistical implements OnInit {
    @Input() inValue: any;
    msgs: Message[] = [];
    userform: FormGroup;
    message: any = { msg: [] };
    tabMesg: any = tableMessage;//表格中无数据时显示的内容
    code: any;
    first: number = 0;//分页控制
    pageNum: number = 1;//当前页数
    pageSize: number = 10;//每页条数
    totalNum: number;//总条数
    dataList: any = [];
    groupTypeList: any;//群类型
    approvalStatusList: any;//平价人状态
    display: boolean = false;//模态框
    orgTreeDisplay: boolean = false;//模态框
    ch: any = CALENDAR_CN;//日期控件
    DOWNLOAD: string = TZB_CUS_DOWNLOAD;
    constructor(
        public httpService: CusOperationHttpService,
        private commonFunc: Customtools,
        public commfunc: Commfunc,
        public fb: FormBuilder,
        public router: Router,
    ) {
        this.userform = fb.group({
            orgNo: [''],//经办机构
            groupType: [''],//客群类型
            groupId: ['', [Validators.maxLength(20), checkNumber]],//群号
            groupName: ['', [Validators.maxLength(255), specialSymbol]],//群名称
            groupOwnerName: ['', [Validators.maxLength(255), specialSymbol]],//群主
            createdStampStart: [''],//群创建时间
            createdStampEnd: [''],//群创建时间
        })
    }
    ngOnInit() {
        this.codeValues() //调用方法，获取全部码值
        this.groupTypeList = this.code['GroupTypeCreate'];
        this.approvalStatusList = this.code['approvalStatus'];
        this.queryClick();
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    // 选择机构/选择员工
    openTree() {
        this.orgTreeDisplay = !this.orgTreeDisplay;
    }
    // 关闭-机构模态框
    contacttreeCall(param) {
        this.orgTreeDisplay = false;
        this.userform.get('orgNo').setValue(param.orgId);
    }
    // 开始、结束日期判断
    showTime(param1, param2) {
        if (param1 == 'plan') {
            const createdStampStart = this.commfunc.transDateN(this.userform.value.createdStampStart);
            const createdStampEnd = this.commfunc.transDateN(this.userform.value.createdStampEnd);
            if (createdStampStart && createdStampEnd) {
                if (createdStampStart > createdStampEnd) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: '开始日期不能大于结束日期' });
                    setTimeout(() => {
                        if (param2 == "start") {
                            this.userform.value.createdStampStart = null;
                        } else {
                            this.userform.value.planContactTimeTo = null;
                        }
                    })
                }
            }
        }
    }
    // 手动输入日期
    handInputDate(param1, param2) {
        this.showTime(param1, param2);
    }
    //查询
    query() {
        this.pageSize = 10;
        this.pageNum = 1;
        this.first = 0;
        this.queryClick();
    }
    //列表查询
    queryClick() {
        const param = Object.assign({}, this.userform.value);
        param['pageSize'] = this.pageSize;
        param['pageNum'] = this.pageNum;
        param['operationType'] = 0;
        param['createdStampStart'] = this.commfunc.transDateN(this.userform.value.createdStampStart);
        param['createdStampEnd'] = this.commfunc.transDateN(this.userform.value.createdStampEnd);
        this.httpService.queryCustGroupStatisticsMain(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.dataList = data.resultList ? data.resultList : [];
                this.totalNum = data.total;
                this.msgs = [];
                this.msgs = [{ severity: 'success', summary: '提示', detail: '查询成功！' }];
            } else {
                this.dataList = [];
                this.msgs = [];
                data.returnCode.message ? data.returnCode.message : '查询失败！';
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
        });
    }
    //下载
    download() {
        if (this.dataList.length == 0) {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '没有数据无法下载!' }];
            return;
        }
        const param = {};
        param['report'] = '02';
        this.httpService.groupAdminStatementExport(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.router.navigate(["pages/tzb/custom/downloadCenter/downCenter"])
                this.msgs = [];
                this.msgs = [{ severity: 'success', summary: '提示', detail: '导出成功！' }];
            } else {
                this.msgs = [];
                data.returnCode.message ? data.returnCode.message : '导出失败！';
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
        });
    }
    // 重置
    reset() {
        this.userform.get('orgNo').setValue('');
        this.userform.get('groupType').setValue('');
        this.userform.get('groupId').setValue('');
        this.userform.get('groupName').setValue('');
        this.userform.get('groupOwnerName').setValue('');
        this.userform.get('createdStampStart').setValue('');
        this.userform.get('createdStampEnd').setValue('');
    }
    paginate(event) {
        //每页显示的条数
        this.pageSize = event.rows;
        this.pageNum = event.page + 1;
        this.first = event.first;
        this.queryClick();
    }
}