  choose(event, i) {
    // 四级区划再选择
    let params = {
      parentZoneId: event.value
    }
    let successFnc = data => {
      let list = [{ label: '请选择', value: '' }];
      data.zoneList.forEach(item => {
        list.push({
          label: item.zoneName,
          value: item.zoneId
        })
      })
      switch (i) {
        case 1:
          this.cengJi_2 = false;
          this.cengJi_3 = false;
          this.cengJi_4 = false;
          list.length != 1 ? this.cengJi_2 = true : '';
          this.regionCity = list;

          this.regionCity1 = '';
          this.regionArea1 = '';
          this.regionStreet1 = '';
          break;
        case 2:
          this.cengJi_3 = false;
          this.cengJi_4 = false;
          list.length != 1 ? this.cengJi_3 = true : '';
          this.regionArea = list;

          this.regionArea1 = '';
          this.regionStreet1 = '';
          break;
        case 3:
          this.cengJi_4 = false;
          list.length != 1 ? this.cengJi_4 = true : '';
          this.regionStreet = list;


          this.regionStreet1 = '';
          break;
        default: break;
      }
    }
    if (i == '1' || i == '2') {
      this.custGroupInfoBean.groupAllName = this.custGroupInfoBean.groupName;
    }
    if (i == '3') {
      if (event.value != '') {
        for (let e = 0; e < this.regionArea.length; e++) {
          if (this.regionArea1 == this.regionArea[e].value) {
            this.fullName = this.regionArea[e].label;
            this.tempName = this.fullName;
            if (this.custGroupInfoBean.groupType == '7') {
              this.custGroupInfoBean.groupAllName = this.fullName + this.custGroupInfoBean.groupName;
            }
          }
        }
      } else if (event.value == '') {
        this.custGroupInfoBean.groupAllName = this.custGroupInfoBean.groupName;
      }
    }
    if (i == '4') {
      if (event.value != '') {
        for (let e = 0; e < this.regionStreet.length; e++) {
          if (this.regionStreet1 == this.regionStreet[e].value) {
            this.fullName = this.tempName + this.regionStreet[e].label;
            if (this.custGroupInfoBean.groupType == '7') {
              this.custGroupInfoBean.groupAllName = this.fullName + this.custGroupInfoBean.groupName;
            }
          }
        }
      } else if (event.value == '') {
        this.custGroupInfoBean.groupAllName = this.tempName + this.custGroupInfoBean.groupName;
      }
    }
    if (i == '5') {
      if (event.value == 4) {
        this.isHangye = true;
        this.custGroupInfoBean.groupAllName = this.custGroupInfoBean.groupName;
      } else if (event.value == 7) {
        this.isHangye = false;
        if (this.regionArea1 && this.regionStreet1) {
          for (let e = 0; e < this.regionArea.length; e++) {
            if (this.regionArea1 == this.regionArea[e].value) {
              this.fullName = this.regionArea[e].label;
              this.tempName = this.fullName;
            }
          }
          for (let e = 0; e < this.regionStreet.length; e++) {
            if (this.regionStreet1 == this.regionStreet[e].value) {
              this.fullName = this.tempName + this.regionStreet[e].label;
            }
          }
        } else if (this.regionArea1) {
          for (let e = 0; e < this.regionArea.length; e++) {
            if (this.regionArea1 == this.regionArea[e].value) {
              this.fullName = this.regionArea[e].label;
              this.tempName = this.fullName;
            }
          }
        } else if (this.regionStreet1) {
          for (let e = 0; e < this.regionStreet.length; e++) {
            if (this.regionStreet1 == this.regionStreet[e].value) {
              this.fullName = this.tempName + this.regionStreet[e].label;
            }
          }
        }
        this.custGroupInfoBean.groupAllName = this.fullName + this.custGroupInfoBean.groupName;
      } else {
        this.isHangye = false;
        this.custGroupInfoBean.groupAllName = this.custGroupInfoBean.groupName;
      }
    }
    this.commfunc.interfaceComFnc(this.httpService.queryZoneInfo(params), successFnc, this.msgs)
  }
  //群名
  nameChange() {
    if (this.custGroupInfoBean.groupType === '7' && (this.regionArea1 != '' || this.regionStreet1 != '')) {
      for (let e = 0; e < this.regionArea.length; e++) {
        if (this.regionArea1 == this.regionArea[e].value) {
          this.fullName = this.regionArea[e].label;
          this.tempName = this.fullName;
        }
      }
      for (let e = 0; e < this.regionStreet.length; e++) {
        if (this.regionStreet1 == this.regionStreet[e].value) {
          this.fullName = this.tempName + this.regionStreet[e].label;
        }
      }
      this.custGroupInfoBean.groupAllName = this.fullName + this.custGroupInfoBean.groupName;
    } else {
      this.custGroupInfoBean.groupAllName = this.custGroupInfoBean.groupName;
    }
  }
  //详细地址
  createMap() { }
  queryParentZoneAll(region) {
    let params = {
      zoneId: region
    }
    let successFnc = data => {
      this.regionSHeng = [{ label: '请选择', value: '' }];
      data.firstZone.forEach(item => {
        this.regionSHeng.push({
          label: item.zoneName,
          value: item.zoneId
        })
      });

      data.secondZone.forEach(item => {
        this.regionCity.push({
          label: item.zoneName,
          value: item.zoneId
        })
      });

      data.thirdZone.forEach(item => {
        this.regionArea.push({
          label: item.zoneName,
          value: item.zoneId
        })
      });

      data.fourthZone.forEach(item => {
        this.regionStreet.push({
          label: item.zoneName,
          value: item.zoneId
        })
      });


      this.regionSHeng = _.clone(this.regionSHeng)
      this.regionCity = _.clone(this.regionCity)
      this.regionArea = _.clone(this.regionArea)
      this.regionStreet = _.clone(this.regionStreet)
      data.zoneList.forEach(item => {
        switch (item.parentZoneLevel) {
          case '1': this.regionSHeng1 = item.parentZoneId; break;
          case '2': this.cengJi_2 = true; this.regionCity1 = item.parentZoneId; break;
          case '3': this.cengJi_3 = true; this.regionArea1 = item.parentZoneId; break;
          case '4': this.cengJi_4 = true; this.regionStreet1 = item.parentZoneId; break;
          default: break;
        }
      })
    }
    this.commfunc.interfaceComFnc(this.httpService.queryParentZoneAll(params), successFnc, this.msgs);
    // if (!this.region || this.region == '') {
    //   this.regionSHeng1 = '';
    // }
  }


