import { Component, OnInit, Output, Input, EventEmitter, } from '@angular/core';
import { ActivatedRoute, Params, Router } from '@angular/router';
import { SelectItem, Message, AutoCompleteModule, ConfirmationService } from 'primeng/primeng';
import * as _ from 'lodash';
import { GroupAdminInfoBean, CustGroupInfoBean, roleList } from './../../bean/custom-group.bean';
import { CusOperationHttpService } from './../../../http/custom-operation/custom-operation.http.service';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { TZB_CUS_UPLOAD, TZB_CUS_DOWNLOAD, TZB_HTTP_YXKQ } from 'app/@uisftech/common/service/http.constant';
import { CustGroupListBean } from 'app/pages/tzb/custom/custom-marketing/group/bean/custom-group.bean';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';

@Component({
    selector: 'customGroup-list-exam',
    templateUrl: './customGroup-list-exam.html',
    styleUrls: ['./customGroup-list-exam.scss'],
    providers: [CusOperationHttpService, ConfirmationService]
})
export class customGroupListExamComponent implements OnInit {
    //公共
    tableMesg: any = tableMessage;
    // 控件
    location_params: any;
    role_list: any;
    msgs: any;
    header_1: any;
    change_prev: any;
    change_after: any;
    remark: string;
    isContactPerson: boolean;
    //------------
    applyType: any;
    groupId: string;
    groupName: string;
    institution: any;
    staff: any;
    obj_info: any;
    list_arr: any = [];
    groupUploadFlag: string;
    UPLOAD: string = TZB_CUS_UPLOAD;//上传模板
    DOWNLOAD: string = TZB_CUS_DOWNLOAD;//下载模板
    fileList: any = [];//上传文件列表
    files: any = [];//
    custGroupListBean: CustGroupListBean = new CustGroupListBean();//客户群名单
    batchDisplay: boolean = false;//批量导入
    addDisplay: boolean = false;//返回结果
    globalSeqNoImage: string;//影像流水号
    title1: string = '客户群名单批量导入';
    title2: string = '客户群名单导入结果';
    CustGroupInfoBean: CustGroupInfoBean = new CustGroupInfoBean;
    taskCode: string;
    code: any[];
    relType: any[];
    idenType: any[];
    detailList: any = [];
    taskRate: string;
    canUp: boolean = false;
    flag: string;
    isHistory: boolean = false;
    list: any;//审批接收data
    // 初始化
    tempArr: any = [];
    isNoValue: any = [
        { label: '请选择', value: '' },
        { label: '是', value: '1' },
        { label: '否', value: '0' },
    ]
    custGroupList: any = {};
    addDisply: boolean = false;
    addTitle: any;
    addType: string;
    property: boolean = true;
    customListInfo: any = {};
    constructor(
        public httpService: CusOperationHttpService,
        public route: ActivatedRoute,
        public commfunc: Commfunc,
        public confirmationService: ConfirmationService
    ) {
        this.codeValues();
        this.relType = this.code['CustomGroupRelType'];
        this.idenType = this.code['CustomGroupIdenType'];
    }
    ngOnInit() {
        if (this.route.snapshot.params['markFlag'] && this.route.snapshot.params['markFlag'] == '2') {
            this.property = false;
        }
        
        this.detailList = this.route.snapshot.params;
        this.list = JSON.parse(this.route.snapshot.params.dataMap)
        this.taskRate = this.route.snapshot.params['taskRate'];
        this.flag = this.route.snapshot.params['markFlag'];
        if (this.flag == '1') {
            this.isHistory = false;
        } else {
            this.isHistory = true;
        }
        if (this.taskRate == '1' && !this.isHistory) {
            this.canUp = true;
        } else {
            this.canUp = false;
        }
        this.location_params = this.route.snapshot.params;
        this.groupUploadFlag = this.location_params.groupUploadFlag;
        this.globalSeqNoImage = this.location_params.globalSeqNoImage;
        this.applyType = this.location_params.applyType;
        this.taskCode = this.location_params.taskCode;
        // this.header_1 = this.location_params.header_1;
        this.isContactPerson = this.location_params.roleType == 1 ? true : false;
        let i = this.location_params.roleType;
        if (i == 4) {
            this.header_1 = '群评价人';
        } else if (i == 5) {
            this.header_1 = '群主';
        } else if (i == 3) {
            this.header_1 = '群指导师傅';
        } else if (i == 1) {
            this.header_1 = '群联系人';
        } else {
            this.header_1 = '群介绍人';
        }
        this.selectCustGroupInfo()
        this.selectCustGroupList()
    }

    selectGroupAdminInfo(statusId) {
        let params = {
            groupId: this.route.snapshot.params['groupId'],
            roleType: this.route.snapshot.params['roleType'],
            statusId: statusId,
        }
        // 客户群管理人员再查询
        this.httpService.selectGroupAdminInfo(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (statusId == 1) {
                    this.change_prev = [];
                    if (data.roleList != null) {
                        data.roleList.forEach(item => {
                            this.change_prev.push({
                                userId: item.userId,
                                userName: item.userName,
                                groupZone: item.groupZone
                            })
                        });
                    }
                } else if (statusId == 3) {
                    this.change_after = [];
                    if (data.roleList != null) {
                        data.roleList.forEach(item => {
                            this.change_after.push({
                                userId: item.userId,
                                userName: item.userName,
                                groupZone: item.groupZone
                            })
                        });
                    }
                }

            } else {
                this.msgs = [];
                data.returnCode.code ? data.returnCode.code : '查询失败';
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
        })
    }
    confirm(apply_result) {
        let params = {
            groupId: this.location_params.groupId,
            roleType: this.location_params.roleType,
            remark: this.remark,
            apprResult: apply_result,
            roleList: this.change_prev,
            applyRoleList: this.change_after
        }
        // 客户群管理人员变更申请
        this.httpService.updateGroupAdminStatus(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.msgs = [];
                this.msgs = [{ severity: 'success', summary: '提示', detail: '变更成功' }];
            } else {
                this.msgs = [];
                data.returnCode.code ? data.returnCode.code : '查询失败';
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
        })
    }

    selectCustGroupList() {
        debugger
        let params = {
            groupId: this.location_params.groupId,
            listType: 1,
            batchId: this.location_params.batchId,
            pageNum: 1,
            pageSize: 10000
        }
        // 客户群信息查询
        this.httpService.selectCustGroupList(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.CustGroupInfoBean = data;

                this.custGroupList['custList'] = _.clone(data.custList);
                this.custGroupList['custNum'] = _.clone(data.custNum);
                this.custGroupList['doorNum'] = _.clone(data.doorNum);
                this.custGroupList['batchId'] = this.location_params.batchId,
                    console.log(this.custGroupList);

                this.list_arr = data.custList;
                this.list_arr.forEach((item, i) => {
                    if (this.list_arr[i].targetCustFlag && this.list_arr[i].targetCustFlag != '') {
                        if (this.list_arr[i].targetCustFlag == 1) {
                            this.list_arr[i].targetCustFlag = '是';
                        } else if (this.list_arr[i].targetCustFlag == 0) {
                            this.list_arr[i].targetCustFlag = '否';
                        }
                    }
                    if (this.list_arr[i].effFlag && this.list_arr[i].effFlag != '') {
                        if (this.list_arr[i].effFlag == 1) {
                            this.list_arr[i].effFlag = '是';
                        } else if (this.list_arr[i].effFlag == 0) {
                            this.list_arr[i].effFlag = '否';
                        }
                    }
                });
                this.list_arr = _.clone(this.list_arr)

            }
            else {
                this.msgs = [];
                data.returnCode.code ? data.returnCode.code : '查询失败！';
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败！' }];
        })
    }  // 判断是否临时客户
    isQl(col) {
        if (col.custNo.search(/^QL/)) {
            return true;
        }
        return false;
    }
    selectCustGroupInfo() {
        debugger
        let params = {
            groupId: this.location_params.groupId,
            status: 3,
            batchId: this.location_params.batchId,
        }
        // 客户群信息查询
        this.httpService.selectCustGroupInfo(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.groupId = data.groupId;
                this.groupName = data.groupName;
                this.custGroupList['groupId'] = _.clone(data.groupId);
                this.custGroupList['groupName'] = _.clone(data.groupName);
            }
            else {
                this.msgs = [];
                data.returnCode.code ? data.returnCode.code : '查询失败！';
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败！' }];
        })
    }

    //名单附件下载
    listAttachDownload() {
        // let a = encodeURI(this.custGroupInfoBean.groupName);
        // let b = `groupId=${this.custGroupInfoBean.groupId};groupName=${a}`;
        // window.open('/#/pages/tzb/custom/custom-group/custom-group-view/custom-image-query;' + b);
        if (!this.globalSeqNoImage || this.globalSeqNoImage == 'undefined') {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '无此附件' }];
            return
        }
        //获取用户名及ID
        let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        let chName = JSON.parse(this.commfunc.getSessionDataCH('chName'));
        let orgID = commonHeader.orgId;
        let userId = commonHeader.userId;
        let orgName = chName.orgName;
        let userName = chName.userName;
        let time = new Date();
        let year = time.getFullYear();
        let month = time.getMonth() + 1 + '';
        let datas = time.getDate() + '';
        if (month.length < 2) {
            month = '0' + month;
        }
        if (datas.length < 2) {
            datas = "0" + datas
        }
        let QUERY_TIME = '' + year + month;
        window.open(TZB_HTTP_YXKQ + "SunIAS_V7/SunIASRequestServlet.do?UID=MOBILE&OrgID=" + orgID + "&OrgName=" + orgName + "&OrgOwner=" + orgID + "&right=0100000&PWD=cyg&UserID=" + userId + "&UserName=" + userName + "&AppID=SA&info1=BUSI_SERIAL_NO:" + this.globalSeqNoImage + ";OBJECT_NAME:APP1009;FILELEVEL:33;RIGHT:1111111;OBJECT_NAME:APP1009;QUERY_TIME:" + QUERY_TIME)
    }

    //批量导入名单
    importList() {
        this.batchDisplay = true;
    }

    // 客户群名单批量导入关闭
    closebatch() {
        this.batchDisplay = false;
    }

    // 客户群名单返回结果模态框
    returnCustGroupList() {
        this.addDisplay = false;
    }

    //返回客户群
    toCustGroupView() {
        if (this.applyType == 0) {
            return
        }
        let groupId = this.commfunc.compileStr(this.groupId);
        let a = 'groupId=' + groupId;
        window.open('/#/pages/tzb/custom/custom-group/custom-group-view;' + a);
    }

    //批量导入
    onBeforeSend(event) {
        // 上传点击,需跨域
        this.custGroupListBean = new CustGroupListBean();
        if (event.xhr.response) {
            this.fileList = JSON.parse(event.xhr.response);
            if (this.fileList.length > 1) {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '不支持上传多个文件！' }];
                return;
            }
            if (this.fileList.hasOwnProperty('errorMsg')) {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: this.fileList.errorMsg });
                return
            }

            this.custGroupListBean.groupId = this.groupId;
            this.custGroupListBean.filePath = this.fileList[0].fileUrl;
            this.custGroupListBean.fileName = this.fileList[0].fileName;
        }
        else {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '获取服务器列表失败！' }];
        }

        //不需要审批
        let param = this.custGroupListBean;
        this.custGroupListBean.groupApplyFlag = '1';
        this.httpService.batchCustGroupList(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (parseInt(data.successNum) > 0) {
                    let dataMap = {
                        applyType: this.applyType,
                        groupId: this.groupId,
                        batchId: data.batchId
                    }
                    this.commfunc.setSessionDataCH('dataMap', JSON.stringify(dataMap));
                }

                this.custGroupListBean.successNum = data.successNum;
                this.custGroupListBean.failNum = data.failNum;

                data.failInfoList = data.failInfoList.filter((item) => { return item.executeFlag == 'F' });//
                this.custGroupListBean.failInfoList = data.failInfoList;
                this.batchDisplay = false;
                this.addDisplay = true;
                this.msgs = [];
                this.msgs = [{ severity: 'success', summary: '提示', detail: data.returnCode.message }];
            }
            else {
                this.msgs = [];
                data.returnCode.code ? data.returnCode.code : '导入失败！';
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败！' }];
        })
    }

    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }

    addArray(index) {
        // 增加行
        this.tempArr.push({
            doorNum: '',
            custNo: '',
            custName: '',
            // relType: '',
            relType: "",
            relDesc: '',
            contacts: '',
            idenType: '',
            idenNum: '',
            address: '',
            telNum: '',
            industry: '',
            description: '',
            xnkBz: '',
            hzdbjjFlag: '',
            cdhykFlag: '',
            syydFlag: '',
            xydFlag: '',
            xjdFlag: '',
            fydFlag: '',
            khjsFlag: '',
            qtywFlag: '',
            xxfFlag: '',
            ajdFlag: '',
            targetCustFlag: ''
        });
        this.list_arr = _.clone(this.tempArr);
        this.custGroupList['custList'] = _.clone(this.list_arr);
    }

    // 删除
    delArray(col) {
        this.confirmationService.confirm({
            message: '确认要删除吗?',
            accept: () => {
                let param = {
                    optType: '1',
                    groupId: this.route.snapshot.params['groupId'],
                    groupName: this.route.snapshot.params['groupName'],
                    batchId: this.route.snapshot.params['batchId'],
                    custList: [{ custNo: col.custNo }],
                }
                this.httpService.updateCustGroupListByUser(param).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        this.selectCustGroupInfo()
                        this.selectCustGroupList()
                    } else {
                        this.msgs = [];
                        data.returnCode.code ? data.returnCode.code : '新增失败';
                        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
                    }
                }, err => {
                    this.msgs = [];
                    this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败！' }];
                })
            }
        });
    }

    // 新增-名单导入
    import(type) {
        this.addDisply = true;
        this.addTitle = '';
        this.addType = type;
        if (type == '1') {
            this.addTitle = '群客户导入';
        } else if (type == '2') {
            this.addTitle = '模板批量导入';
        }
    }

    // 关闭-名单导入
    addCall(event, index) {
        this.addDisply = false;
        this.selectCustGroupInfo()
        this.selectCustGroupList()
        if (index == 3) {
            this.customListInfo["globalSeqNoImage"] = event;
            console.log(this.customListInfo);
        }
    }

}