import { Component, OnInit } from '@angular/core';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { Message } from "primeng/components/common/message";
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { tableMessage } from "app/@uisftech/common/constant/config";
import { CusOperationHttpService } from "app/pages/tzb/custom/http/custom-operation/custom-operation.http.service";
import { Customtools } from "app/pages/tzb/custom/http/customUtil";
import { specialSymbol } from "app/@uisftech/common/service/validator";
import { Router, ActivatedRoute } from "@angular/router";

@Component({
    selector: 'group-evaluator-summary',
    templateUrl: './group-evaluator-summary.component.html',
    styleUrls: ['./group-evaluator-summary.component.scss'],
})
export class GroupEvaluatorSummary implements OnInit {
    msgs: Message[] = [];
    userform: FormGroup;
    tabMesg: any = tableMessage;//表格中无数据时显示的内容
    code: any;
    first: number = 0;//分页控制
    pageNum: number = 1;//当前页数
    pageSize: number = 10;//每页条数
    totalNum: number;//总条数
    dataList: any = [];
    partyClassificationGroupId: any;
    constructor(
        public httpService: CusOperationHttpService,
        public routerInfo: ActivatedRoute,
        private commonFunc: Customtools,
        public commfunc: Commfunc,
        public router: Router,
        public fb: FormBuilder,
    ) {
        this.partyClassificationGroupId = this.routerInfo.snapshot.params['partyClassificationGroupId'];
        this.userform = fb.group({
            evalPartyId: ['', [Validators.maxLength(20), specialSymbol]],//评价人工号
            evalPartyName: ['', [Validators.maxLength(255), specialSymbol]],//评价人名称
            roleType: ['4'],//角色类型
            partyClassificationGroupId: [''],//群号
        })
    }
    ngOnInit() {
        this.codeValues() //调用方法，获取全部码值
        this.userform.get('partyClassificationGroupId').setValue(this.partyClassificationGroupId);
        this.queryClick();
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    //查询
    query() {
        this.pageSize = 10;
        this.pageNum = 1;
        this.first = 0;
        this.queryClick();
    }
    //列表查询
    queryClick() {
        const param = Object.assign({}, this.userform.value);
        param['pageSize'] = this.pageSize;
        param['pageNum'] = this.pageNum;
        this.httpService.groupAdminStatisicsQuery(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.dataList = data.resultList ? data.resultList : [];
                this.totalNum = data.total;
                this.msgs = [];
                this.msgs = [{ severity: 'success', summary: '提示', detail: '查询成功！' }];
            } else {
                this.dataList = [];
                this.msgs = [];
                data.returnCode.message ? data.returnCode.message : '查询失败！';
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
        });
    }
    //跳转第三方明细
    toGroupView(col, i) {
        if (i == '1') {
            col.targetCustFlag = '';
        } else if (i == '2') {
            col.targetCustFlag = '0';
        } else if (i == '3') {
            col.targetCustFlag = '1';
        }
        if (!col.evalPartyName) {
            col.evalPartyName = '';
        }
        if (!col.evalPartyId) {
            col.evalPartyId = '';
        }
        this.router.navigate(['/pages/tzb/custom/custom-group/custom-group-view/group-evaluator-detail', { evalPartyId: col.evalPartyId, evalPartyName: col.evalPartyName, targetCustFlag: col.targetCustFlag, groupId: this.partyClassificationGroupId }]);
    }
    // 重置
    reset() {
        this.userform.get('evalPartyId').setValue('');
        this.userform.get('evalPartyName').setValue('');
    }
    paginate(event) {
        //每页显示的条数
        this.pageSize = event.rows;
        this.pageNum = event.page + 1;
        this.first = event.first;
        this.queryClick();
    }
    //返回视图
    return() {
        window.history.go(-1);
    }
}