import { Component, OnInit, Output, Input, EventEmitter } from '@angular/core';
import { SelectItem, Message, AutoCompleteModule } from 'primeng/primeng';
import * as _ from 'lodash';
import { CustGroupInfoBean } from './../../bean/custom-group.bean';
import { CusOperationHttpService } from './../../../http/custom-operation/custom-operation.http.service';
import { Commfunc } from "app/@uisftech/common/service/commonFun";

@Component({
    selector: 'owner-name-edit',
    templateUrl: './owner-name-edit.html',
    styleUrls: ['./owner-name-edit.scss'],
	providers: [Commfunc]
})
export class ownerNameEditComponent implements OnInit {
    @Output() outValue: EventEmitter<any> = new EventEmitter<any>();
    @Input() inValue: any;
    @Input() list: any;
    @Input() GroupAdminFlag: any;
    

    // 控件
     msgs: Message[] = [];
     filteredBrands: any = [];
     brand: any = '';

    //中间量
     btnName: any = '';
   
     groupAdminNameShow: any = '';

    // 假数据
    brands: string[] = ['Audi', 'BMW', 'Fiat', 'Ford', 'Honda', 'Jaguar', 'Mercedes', 'Renault', 'Volvo', 'VW'];

    // bean
     custGroupInfoBean: CustGroupInfoBean = new CustGroupInfoBean();//管理人员


    constructor(public commfunc: Commfunc,public httpService: CusOperationHttpService) {

    }
    ngOnInit() {
        this.preSet()
    }
    preSet() {
        // 高级权限
        this.btnName = '确定调整';
        // 低级权限
        // this.btnName = '提交调整'
    }
    reUpdateCustGroupInfo() {
        // 高级权限，直接修改
        this.httpService.updateCustGroupInfo(this.inValue).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.inValue = data;
            } else {
                this.msgs = [];
                data.returnCode.code ? data.returnCode.code : '修改失败';
                this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
        })
    }
    return() {
        // 高级权限
        if (this.GroupAdminFlag != '1' && this.GroupAdminFlag != '3' &&this.GroupAdminFlag == '0'){
            this.reUpdateCustGroupInfo();  
            this.outValue.emit({ ownerNameDisplay: false, ownerName: this.inValue.ownerName,flag:this.GroupAdminFlag });
        }
        else if ( this.GroupAdminFlag == '1' ) {
            this.outValue.emit({ ownerNameDisplay: false, ownerName: this.brand,flag:this.GroupAdminFlag });            
        }
        else if ( this.GroupAdminFlag == '3' ) {
            this.outValue.emit({ ownerNameDisplay: false, ownerName: this.brand,flag:this.GroupAdminFlag }); 
        }
        // 低级权限
        // 重置
        this.brand = '';
    }
    filterBrands(event) {
        // 模糊查询,需要其他接口
        this.filteredBrands = [];
        for (let i = 0; i < this.brands.length; i++) {
            let brand = this.brands[i];
            if (brand.toLowerCase().indexOf(event.query.toLowerCase()) == 0) {
                this.filteredBrands.push(brand);
            }
        }
    }
    //按钮权限
	permissionCheck(id){
		return	this.commfunc.permissionCheck(id)
	  }
}